.wrapper {
  display: grid;
  grid-template-columns: repeat(2, 2fr);
}

.eventList {
  display: grid;
  grid-template-rows: 200px;
}


.eventCommands {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  height: 20px;
  margin-top: 20%;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
}

.numberOfEvents {
  display: grid;
  grid-template-columns: 25px 150px 150px 70px;
  margin-bottom: 5px;
}

.numberOfEventsLabel {
  grid-column-start: 3;
  grid-column-end: 4;
  width: 150px;
  margin-top: 8px;
}

.event-number-input {
  width: 70px;
}

.data-vis-wrapper {
  display: grid;
  grid-template-rows: repeat(2, 2fr);
}