  color: white;
}

.welcome-title {
  color: rgb(197, 236, 125)
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}

.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  -webkit-align-self: center;
          align-self: center;
  height: 38px;
  color: black;
}

.city {
  width: 250px;
}

.suggestions {
  width: 250px;
  position: absolute;
  z-index: 2;
  -webkit-align-self: center;
          align-self: center;
  margin: 0;
  top: 43px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions>li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
  color: black;
}

.suggestions>li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;

}

.suggestions>li:hover {
  background-color: rgb(197, 236, 125);
  color: black;
}

.event {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

.seeAllCities {
  color: black;
}

.name {
  font-size: 18px;
  font-weight: 600;
  color: rgb(197, 236, 125)
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.Alert {
  top: 20px;
  text-align: left;
  margin-left: 25px;
  grid-column-start: 1;
  grid-column-end: 5;
  margin-top: 3%;
}

.Alert-text {
  padding-left: 20px;
